import React from "react";
import img from "../pic.png";

function copy(){
    const text = document.querySelector("#text-val");
    text.select()
    text.setSelectionRange(0, 99999)
    document.execCommand("copy");
    alert("copied")
    text.blur();

}

function Content(){
    // const hide_container = document.querySelector(".hide");
    function  contact(){
        let hide_container = document.querySelector(".hide");
        if(hide_container.style.display=="none"){
            hide_container.style.display="block";
            return ;
        }
        hide_container.style.display="none";

        
    }

    return (
        <div className="container">
            <div className="hide"style={{display:"none"}}>
                <button  style={{float:"right",fontSize:"38px"}} onClick={contact}><i className="fa fa-close"></i></button>
                
                <div id="center">
                    <label style={{color:"white"}} for="input">Email:</label>
                    <input   id="text-val" type="text"  value="yogesh1pandey1@gmail.com" />
                    <button onClick={copy}>Copy</button>
                </div>
            </div>
            <div className="about">
                <div className="about-me">
                Hi, <br />
                I'm <span id='yellow'>Yogesh</span>, <br />
                Web Developer. <br />
                <span id="skills">React/Node/Python</span>
                <br />
                <div className="contact">
                    <button onClick={contact}>Contact Me</button>
                </div>
                </div>
            </div>
            
            <div className="imgd">
                <img id="img" src={img} />
            </div>
        </div>
    )
}

export default Content;